import React from 'react';

const sections = [
  { id: 1, val: 'About' },
  { id: 2, val: 'Work' },
  { id: 3, val: 'Services' },
  { id: 4, val: 'Contact' },
  { id: 5, val: 'Map' },
  { id: 6, val: 'busry' },
  { id: 7, val: 'kasim' },
];

const Navlists = (props) => {
  const sectionList = sections.map((data) => {
    const { val, id } = data;
    return (
      <li className="nav-item" key={id}>
        <a
          className={`nav-link js-scroll-trigger ${props.textColr}`}
          href={`#${val.toLowerCase()}`}
        >
          {val}
        </a>
      </li>
    );
  });

  return (
    <div
      className="collapse navbar-collapse Nav-list-container"
      id="navbarResponsive"
    >
      <ul className={`navbar-nav flex-column ml-auto`}>{sectionList}</ul>
    </div>
  );
};

export default Navlists;
