import React from 'react';
import logo from '../assets/images/busry/logo.svg';
import './BrandLogo.css';

function BrandLogo(props) {
  const { textColr } = props;
  return (
    <a
      className={`navbar-brand js-scroll-trigger  ${textColr} tied`}
      href="#page-top"
    >
      <span className="logoImgContainer">
        <img src={logo} className={'App-logo'} alt={'logo'} />
      </span>
      <span className="logo-name">Busry</span>
    </a>
  );
}

export default BrandLogo;
